import React from "react";
import Grid from "@material-ui/core/Grid";
import PropTypes from "prop-types";
import { Typography, Icon } from "@material-ui/core";
import GetApp from "@material-ui/icons/OpenInNew";

class Publication extends React.Component {
  render() {
    const download = (
      <Icon
        type="a"
        href={this.props.url}
        color="secondary"
        component={GetApp}
        onClick={() => window.open(this.props.url, "_blank")}
        style={{cursor: 'pointer'}}
      />
    );

    const retrievedAtJsx = this.props.retrievedAt != null ? <div style={{fontSize: '0.7rem'}}>(Link zuletzt abgerufen: {this.props.retrievedAt})</div> : null

    return (
      <React.Fragment>
        <Grid item xs={3} className="highlight-color">
          <Typography color="primary">
            {this.props.authors} ({this.props.year})
          </Typography>
        </Grid>
        <Grid item xs={8}>
          <Typography color="primary">{this.props.name} {retrievedAtJsx}</Typography>
        </Grid>
        { this.props.url !== null ? <Grid
          item
          container
          align="center"
          justify="center"
          xs={1}
          direction="column"
        >
          {download}
        </Grid> : null }
      </React.Fragment>
    );
  }
}

Publication.propTypes = {
  authors: PropTypes.string.isRequired,
};

export default Publication;
