const vitaFulltext = 'Studium der Biologie und Chemie, Universität zu Köln (1974-1979). Studiendirektorin für die Fächer Biologie und Chemie (1980-2009). Fachleitung Biologie und Kernseminarleitung an Zentren für schulpraktische Lehrerausbildung im Regierungsbezirk Köln (1998-2009). Mitglied der Lehrplankommission Biologie für die gymnasiale Oberstufe, Ministerium für Schule und Bildung des Landes Nordrhein-Westfalen (2007-2009). Entwicklung des Bachelor- und Masterstudiengangs des Fachdidaktik-Studiums Biologie für das Lehramt an Gymnasien, Gesamtschulen sowie an Berufskollegs, Universität zu Köln, Department für Biologie (2009-2011). Leitung der Fachdidaktik Biologie, Universität zu Köln, Department für Biologie (2009-2021). Mitglied der Akkreditierungsagentur ASIIN für Studiengänge der Ingenieurwissenschaften, der Informatik, der Naturwissenschaften und der Mathematik.';

