import { Typography, Grid, Divider, Box, makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    background: theme.palette.secondary.main,
  },
}));

export default function Header(props) {
  const classes = useStyles();

  return (
    <Box mb={3}>
      <Grid item container spacing={1}>
        <Grid item xs={12}>
          <Typography
            variant="h4"
            component="h1"
            gutterBottom
            align="center"
            color="primary"
          >
            Dr. Monika Pohlmann, i.R.
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography
            variant="h5"
            component="h3"
            gutterBottom
            align="center"
            color="secondary"
            style={{ paddingBottom: 20 }}
          >
            {/* Fachdidaktik Biologie, Department für Biologie, Universität zu Köln */}
            Leitung Fachdidaktik Biologie, Department für Biologie, Universität zu Köln
          </Typography>
          <Divider classes={{ root: classes.root }} />
        </Grid>
      </Grid>
    </Box>
  );
}
