import CategoryHeader from "./common/CategoryHeader";
import { Typography, Grid, Icon  } from "@material-ui/core";
import Email from "@material-ui/icons/Email"
import FlexRendering from "./common/FlexRendering";

const letters = "-..@aadeehiikllmmnnnnnooopt";
const order = [18,7,25,16,6,13,26,27,24,10,22,4,5,11,21,1,12,3,14,15,23,20,19,2,9,8,17];

const getContent = () => {
  let res = [];
  order.map((item, idx) => res[item] = letters[idx]);
  return res.join('');
}

export default function Contact(props) {
  const download = (
    <Icon
      type="a"
      href={''}
      color="secondary"
      component={Email}
      onClick={() => {navigator.clipboard.writeText(getContent()); window.alert("Die Email Adresse wurde in Ihre Zwischenablage kopiert.")}}
      style={{cursor: 'pointer'}}
    />
  );

  return (
    <>
      <CategoryHeader category="Kontakt" />
      
      <Grid item container>
        <Grid
          item
          container
          align="center"
          justify="center"
          xs={1}
          direction="column"
        >
          {download}
        </Grid>
        <Grid item xs={3} className="highlight-color">
            <Typography color="primary">Email</Typography>
        </Grid>
        <Grid item xs={8}>
          <FlexRendering letters={letters} order={order} />
        </Grid>
      </Grid>
    </>
  );
}
