import React from "react";
import PropTypes from "prop-types";
import { Typography } from "@material-ui/core";

const getDiv = (character, num) => {
    const style = character === ' ' ? { minWidth: 5 } : { }

    return <Typography key={num} color="primary" style={{...style, order: num, display: 'flex'}}>{character}</Typography>
}

class FlexRendering extends React.Component {
  render() { 
    const jsx = this.props.order.map((item, idx) => getDiv(this.props.letters[idx], item));

    return <div style={{display:'flex', flexDirection:'row', userSelect:'none'}}>{jsx}</div>;
  }
}

FlexRendering.propTypes = {
  letters: PropTypes.string.isRequired,
  order: PropTypes.arrayOf(PropTypes.number).isRequired,
};

export default FlexRendering;
