import { Container, Typography } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import CategoryHeader from "../components/common/CategoryHeader";
import FlexRendering from "../components/common/FlexRendering";
import KeyValuePair from "../components/common/KeyValuePair";
import SimpleValue from "../components/common/SimpleValue";

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: "center",
      color: theme.palette.text.secondary,
    },
  }));
  
const mletters = "-..@aadeehiikllmmnnnnnooopt";
const morder = [18,7,25,16,6,13,26,27,24,10,22,4,5,11,21,1,12,3,14,15,23,20,19,2,9,8,17];

const sletters = "  -.18abBdeeEgnrrrrSty";
const sorder = [15, 20, 8, 19, 22, 21, 5, 11, 1, 7, 2, 12, 9, 14, 4, 13, 18, 6, 3, 16, 17, 10];

const lletters = "12457aabBccdegGhhilrs  ";
const lorder = [2, 4, 3, 1, 5, 19, 16, 18, 6, 20, 12, 17, 7, 9, 14, 13, 21, 10, 15, 8, 11, 5, 13];

const addr = ['Dr. Monika Pohlmann'];

export default function Imprint() {
    const classes = useStyles();
  
    return (
        <Container maxWidth="md" style={{paddingTop: 50}}>
              <div className={classes.root}>
              <Typography
              variant="h5"
              component="h1"
              gutterBottom
              align="center"
              color="primary"
            >
              Impressum
            </Typography>
                
                <Grid item container>
                    <Grid item xs={12} md={6}>
                      <CategoryHeader category="Verantortlich für den Inhalt" left/>
                      { addr.map(a => (<SimpleValue key={a} content={a}/>)) }
                      <FlexRendering letters={sletters} order={sorder} />
                      <FlexRendering letters={lletters} order={lorder} />
                    </Grid>

                    <Grid item xs={12} md={6}>
                      <CategoryHeader category="Kontakt" left/>
                      <KeyValuePair label="Email">
                        <FlexRendering letters={mletters} order={morder} />
                      </KeyValuePair>
                      <KeyValuePair label="Internet"><a href="https://drpohlmann.github.io">drpohlmann.github.io</a></KeyValuePair>
                    </Grid>
                </Grid>

                {/* <Grid item style={{paddingTop: 50}}>
                  <Typography color="primary" style={{textAlign: 'center'}}>Wir nehmen nicht an einem Streitbeilegungsverfahren vor einer Verbraucherschlichtungsstelle teil.</Typography> 
                </Grid> */}
            </div>
        </Container>
    );
  };