export default function DataProtection() {
    const classes = useStyles();
  
    return (
        <Container maxWidth="md" style={{paddingTop: 50}}>
              <div className={classes.root}>
              <Typography
              variant="h5"
              component="h1"
              gutterBottom
              align="center"
              color="primary"
            >
              Datenschutzerklärung
            </Typography>
                
              <Grid item container>
                { dataProtectionLines.map(l => (
                  <Grid item xs={12} md={6}>
                    <CategoryHeader category={l.heading} />
                    <Typography color="primary">{l.content}</Typography>
                  </Grid>
                ))}      
              </Grid>
            </div>
        </Container>
    );
  };